"use client";
import React, { useCallback } from "react";

import TextInput from "../../shared/TextInput";
import useForm from "@/app/_hook/useForm";
import Logo from "../../Logo/Logo";
import { useRouter } from "next/navigation";
import CustmoizedButton from "../../shared/CustmoizedButton";
import { Input } from "@/components/ui/input";
import { login } from "./actions/action";

const Login = () => {




  return (
    <form
      action={login}
      className="w-80 p-3 bg-secondary rounded-sm text-white"
    >
      <h1 className="text-2xl mb-4">تسجيل دخول</h1>
      <div className="inputs ">
        <div className="grid w-full max-w-sm items-center gap-1.5 mb-3">
          <label htmlFor="username">اسم المستخدم</label>
          <Input
            type={"text"}
            placeplaceholder="UserName"
            name={"userName"}
            className={"text-slate-950 "}
          />
        </div>
        <div className="grid w-full max-w-sm items-center gap-1.5 ">
          <label htmlFor="username">كلمة المرور</label>
          <Input
            type={"text"}
            placeplaceholder="UserName"
            name={"userName"}
            className={"text-slate-950 "}
          />
        </div>
      </div>
      <button type="submit">stubmit</button>
    </form>
  );
};

export default React.memo(Login);
